"use client";

import Image from "next/image";
import Link from "next/link";
import { Heart, MapPin, Bed, Bath, Square } from "lucide-react";
import { useAuthStore } from "@/app/store/useAuthStore";
import { useState } from "react";
import toast from "react-hot-toast";
import { api } from "@/lib/api";

export default function PropertyCard({ property }) {
  const { user, addFavorite, removeFavorite } = useAuthStore();
  const [isFavorite, setIsFavorite] = useState(
    user?.favorites?.includes(property._id) || false,
  );
  const [isLoading, setIsLoading] = useState(false);

  const mainImage =
    property.images.find((img) => img.isPrimary)?.url ||
    property.images[0]?.url ||
    "/placeholder.jpg";

  const handleFavoriteClick = async (e) => {
    e.preventDefault();

    if (!user) {
      toast.error("Please login to add favorites");
      return;
    }

    setIsLoading(true);

    try {
      if (isFavorite) {
        await api.delete(`/properties/${property._id}/favorite`);
        removeFavorite(property._id);
        toast.success("Removed from favorites");
      } else {
        await api.post(`/properties/${property._id}/favorite`);
        addFavorite(property._id);
        toast.success("Added to favorites");
      }
      setIsFavorite(!isFavorite);
    } catch (error) {
      toast.error("Failed to update favorites");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Link href={`/properties/${property._id}`}>
      <div className="card hover:shadow-lg transition-shadow duration-300">
        {/* Property Image */}
        <div className="relative h-48">
          <Image
            src={mainImage}
            alt={property.title}
            fill
            className="object-cover"
            sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
          />
          <div className="absolute top-4 left-4">
            <span
              className={`px-2 py-1 rounded text-xs font-semibold ${
                property.status === "published"
                  ? "bg-green-100 text-green-800"
                  : property.status === "draft"
                    ? "bg-yellow-100 text-yellow-800"
                    : "bg-gray-100 text-gray-800"
              }`}
            >
              {property.status}
            </span>
          </div>
          <button
            onClick={handleFavoriteClick}
            disabled={isLoading}
            className={`absolute top-4 right-4 p-2 rounded-full ${
              isFavorite
                ? "bg-red-500 text-white"
                : "bg-white/80 text-gray-600 hover:bg-white"
            } transition-colors`}
          >
            <Heart className={`w-5 h-5 ${isFavorite ? "fill-current" : ""}`} />
          </button>
        </div>

        {/* Property Details */}
        <div className="p-4">
          <div className="flex justify-between items-start mb-2">
            <h3 className="font-bold text-lg text-gray-900 line-clamp-1">
              {property.title}
            </h3>
            <span className="text-lg font-bold text-primary-600">
              ${property.price.toLocaleString()}
            </span>
          </div>

          <div className="flex items-center text-gray-600 mb-3">
            <MapPin className="w-4 h-4 mr-1" />
            <span className="text-sm truncate">
              {property.location.address}, {property.location.city}
            </span>
          </div>

          <p className="text-gray-600 text-sm mb-4 line-clamp-2">
            {property.description}
          </p>

          {/* Property Features */}
          <div className="flex items-center justify-between text-sm text-gray-600 border-t pt-4">
            {property.bedrooms && (
              <div className="flex items-center">
                <Bed className="w-4 h-4 mr-1" />
                <span>{property.bedrooms} beds</span>
              </div>
            )}
            {property.bathrooms && (
              <div className="flex items-center">
                <Bath className="w-4 h-4 mr-1" />
                <span>{property.bathrooms} baths</span>
              </div>
            )}
            {property.area && (
              <div className="flex items-center">
                <Square className="w-4 h-4 mr-1" />
                <span>{property.area} sqft</span>
              </div>
            )}
          </div>
        </div>
      </div>
    </Link>
  );
}
